import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Link } from '@material-ui/core';
import { Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import LanguageIcon from '@material-ui/icons/Language';
import Layout from 'layout/Layout';
import { LinkPath } from 'wrappers/Wrappers';
import Metadata from 'components/Metadata';
import { ReleasePageMetadata } from 'configs/PageMetadata';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function AboutPage(props) {
  const classes = useStyles();

  return (
    <Layout  >
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Release Notes:
        </Typography>
        <Typography color="textSecondary">[ Saturday - April 10, 2021 ]</Typography>
        <Typography paragraph>
          Today I am pleased to announce the general availability of the refreshed version of my website <LinkPath to="/">www.bipul.in</LinkPath>. I have passionately designed and coded it by myself. This website will be the node for all of my digital presence. The previous version of the website was a blog site, which has been moved to <Link href="https://blogs.bipul.in" target="_blank">blogs.bipul.in</Link>. The refreshed website will have pages with various content which includes my work, experiences, micro-blogs, link to my blog site, contact, appointment scheduling etc.
      </Typography>
        <Typography paragraph>
          <LinkPath to="/">
            <Button
              variant="contained"
              color="primary"
              size="small"
              className={classes.button}
              startIcon={<LanguageIcon />}
            > www.bipul.in
      </Button>
          </LinkPath>
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Technical Design:
        </Typography>
        <Typography paragraph>
          The website has been developed on MicroServices Architecture. At the frontend - ReactJs, GatsBy, Material-UI has been used for rapid and robust UI-UX development. At the backend - Azure Function has been used for creating APIs on .Net Core in C#. Data are stored in Azure CosmosDB and Azure Storage. Frontend of the website has been deployed to GitHub Pages. Continuous Integration and Continuous Deployment (CI-CD) has been implemented with GitHub Actions. The Azure Active Directory has been used to manage the user authentication on this website. Cloudflare has been used to manage the firewall to prevent attacks on the website.
          <br /><br />
          If you come across any issues, please report it on <Link href="https://github.com/BipulRaman/bipulraman.github.io/issues" target="_blank">GitHub Repository</Link>.
      </Typography>
      </Paper>
      <Metadata
        Title={ReleasePageMetadata.Title}
        Description={ReleasePageMetadata.Description}
      />

    </Layout>
  );
}